.description {
    padding-top: 40px;
    padding-bottom: 120px;
  }
  
.holder {
    width: 1076px;
  }
  
 
.list {
    list-style: none;
    display: flex;
    gap: 15px;
  }
  
  
.description__text {
    font-weight: 400;
    font-size: 14px;
    line-height: 140%;
    color: #333333;
    padding: 0;
  
  }
  
  
.connect {
    margin: 40px 110px 80px;
  }
  
.desc {
    display: flex;
    justify-content: space-between;
  }
  
.title {
    font-family: 'Open Sans';
    font-style: normal;
    font-weight: 800;
    font-size: 48px;
    line-height: 140%;
    letter-spacing: 0.01em;
    color: #333333;
    margin-left: 110px;
  }
  
.subtitle {
    margin-top: 60px;
    margin-left: 110px;
    font-family: 'Open Sans';
    font-style: normal;
    font-weight: 700;
    font-size: 24px;
    line-height: 140%;
    color: #333333;
  }
  
.descripthion__list {
  list-style: none;
}

.descripthion__link {
  margin-top: 10px;
  gap: 15px;
  }
    
  
.text {
  margin-top: 40px;
  margin-left: 70px;
  width: 636px;
  }

.benefist {
  font-weight: 400;
  font-size: 14px;
  line-height: 140%;
  color: #333333;  
  }

.benefist::before {
  content: '';
  width: 8px;
  height: 8px;
  background-color: #EC1B2E;
  display: inline-block;
  margin-right: 15px;
  }
  
.lead {
  font-weight: 700;
  font-size: 16px;
  line-height: 140%;
  text-align: center;
  color: #333333;
  margin-top: 65px;
  width: 546px;
  border: 5px solid #EC1B2E;
  padding: 25px 45px;
  margin-left: 70px;
  margin-bottom: 55px;
  }
  
.inner {
  display: flex;
  justify-content: space-between;
  }
  
.capthion {
  margin-top: 60px;
  font-family: 'Open Sans';
  font-style: normal;
  font-weight: 700;
  font-size: 24px;
  line-height: 140%;
  color: #333333;
  margin-left: 110px;
  }

.icon {
  fill: #EC1B2E;
  opacity: 0.05;
  right: 150px;
  top: 198px;
  }  
  
